/* App-related */
main {
    width: 100vw;
    background: #3C3C3D; /* For browsers that do not support gradients */
    background: -webkit-gradient(linear, left top, right top, from(#3C3C3D), color-stop(#4F4F50), to(#3C3C3D));
    background: -webkit-linear-gradient(left, #3C3C3D, #4F4F50, #3C3C3D);
    background: -o-linear-gradient(left, #3C3C3D, #4F4F50, #3C3C3D);
    background: linear-gradient(to right, #3C3C3D, #4F4F50, #3C3C3D); /* Standard syntax (must be last) */
}

/* Containers */
.card {
    padding: 0;
    border-radius: 12px;
    border: 1px solid #DAE1E9;
}


