.form-item {
  position: relative;
  margin-bottom: 0.75rem;
  width: 100%;
  z-index: 1;
}

.form-item .input-item {
  position: relative;
  display: block;
  padding: 0 2rem 0 4rem;
  border: 1px solid rgba(0, 0, 0, 0.08) !important;
  border-radius: 5px;
  width: 100%;
  height: 48px;
  color: #666;
  font-size: .9rem;
  font-weight: 300;
  line-height: 1.7;
  -webkit-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.form-item .input-item:focus {
  border: 1px solid #4F4F50 !important;
}

.form-item .focus {
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  border-radius: 5px;
  z-index: -1;
  width: 100%;
  height: 100%;
  -webkit-box-shadow: 0 0 0 0;
          box-shadow: 0 0 0 0;
  color: #4F4F50;
  -webkit-transition: all .25s ease-in-out;
  -o-transition: all .25s ease-in-out;
  transition: all .25s ease-in-out;
}

.form-item .symbol {
  position: absolute;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 2rem;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  pointer-events: none;
  color: #666;
  font-size: .9rem;
  -webkit-transition: all .25s;
  -o-transition: all .25s;
  transition: all .25s;
}

.form-item .input-item:focus + .focus + .symbol {
  color: #4F4F50 !important;
  padding-left: 28px;
}



