
footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-item-align: end;
        align-self: flex-end;
    padding: 0 64px;
    width: 100%;
}

footer p {
    color: white;
    font-size: 13px;
}

/* Mobile */
@media screen and (max-width: 992px) {
    /* Logo Container */
    footer .logo-container {
        -ms-flex-pack: center;
            justify-content: center;
    }

    /* Links Container */
    footer .links-container {
        padding: 0;
    }

    footer .links-container .menu-container {
        margin-top: 28px;
    }

    footer .links-container .menu ul {
        margin-top: 4px;
    }

    footer .links-container .menu ul li {
        margin-top: 6px;
    }

    /* Copyright */
    footer .copyright-container {
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
        margin-bottom: 24px;
    }

    footer .copyright-container p {
        margin: 4px 0;
    }

    footer .copyright-container .separator {
        display: none;
    }
}


